        </div>
        <div class="col-xs-12 col-md-6">
          <v-select
            v-model="regRole"
            :hint="regRoleFailMsg()"
            :class="{'fail': regRoleVerify, 'pass': regRoleFailMsg()===''}"
            :items="[
              { text: '', value: '' },
              { text: '', value: '' },
              { text: '', value: '' },
              { text: '', value: '' },
              { text: '', value: '' },
              { text: '', value: '' }
            ]"
            label="()"
            persistent-hint/>
        </div>
        <div class="col-xs-12 col-md-6">
          <v-select
            v-model="regGender"
            :hint="regGenderFailMsg()"
            :class="{'fail': regGenderVerify, 'pass': regGenderFailMsg()===''}"
            :items="[
              { text: '', value: '1' },
              { text: '', value: '0' }
            ]"
            label="()"
            persistent-hint/>
        </div>
        <div class="col-xs-12 col-md-6">
          <v-menu
            :v-model="regBirthMenu"
            :close-on-content-click="false"
            :nudge-left="40"
