        </div>
      </div>
      <hr>
      <p class="h3"></p>
      <div class="row flex-center">
        <div class="col-xs-12 col-md-12">
          <p
            class="mx-3 h4"
            style="word-wrap: break-word; white-space: pre-wrap;text-align: justify;"><br><!-- ...<br> -->
            <strong style="color: red;">4MB</strong>
          </p>
        </div>
        <div
          class="col-xs-12 col-md-6 col-xl-4 upload-headshot">
          <div
            v-if="!regHeadshot[0].url"
            class="upload-headshot-add">
            <v-icon>mdi-account</v-icon>
          </div>
          <div
            v-if="regHeadshot[0].url"
            :style="'background-image: url(\''+regHeadshot[0].url+'\')'"
            class="upload-headshot-view">
            <v-icon
              class="cursor"
              @click="cancelHeadshotIMG()">mdi-close</v-icon>
          </div>
          <div class="upload-headshot-file">
            <input
              id="headshot-file"
              type="file"
              data-target="file-uploader"
              accept="image/*"
              persistent-hint
              @change="setHeadshot($event)">
          </div>
        </div>
      </div>
      <hr>
      <p class="h3"></p>
      <div class="row">
        <div class="col-xs-12 col-md-12">
          <p
            class="mx-3 h4"
            style="word-wrap: break-word; white-space: pre-wrap;text-align: justify;"><br>
            <strong style="color: red;">Email</strong><br>
            <strong style="color: red;"></strong>
          </p>
          <v-textarea
            v-model="regNotifiemail"
            :hint="regNotifiemailFailMsg()"
            :class="{'fail': regNotifiemailVerify, 'pass': regNotifiemailFailMsg()===''}"
            label=""
            rows="1"
            type="text"
            persistent-hint/>
        </div>
      </div>
      <hr>
      <p class="h3"></p>
      <div class="row">
        <div class="col-xs-12 col-md-12">
          <p
            class="mx-3 mb-4 h4"
