          <v-btn
            v-if="!userAgreementOpen"
            class="m-3 bg-main-color"
            @click="userAgreementOpen=true">
            <v-icon class="mr-2">mdi-chevron-down</v-icon>
          </v-btn>
          <v-btn
            v-if="userAgreementOpen"
            class="m-3 bg-main-color"
            @click="userAgreementOpen=false">
            <v-icon class="mr-2">mdi-chevron-up</v-icon>
          </v-btn>
          <div
            v-if="userAgreementOpen"
            class="mx-3 user-agreement">
            <!-- <h3 class="text-center"></h3> -->
            <p class="py-2">      </p>
            <p class="py-2">        </p>
            <p class="py-2">        </p>
            <p class="py-2">   </p>
          </div>
          <!-- <v-btn
            v-if="!userAgreementOpen"
            class="m-3 blue float-right"
            @click="userAgreementOpen=true">
            <v-icon class="mr-2">mdi-chevron-down</v-icon>
          </v-btn>
          <v-btn
            v-if="userAgreementOpen"
            class="m-3 blue float-right"
            @click="userAgreementOpen=false">
            <v-icon class="mr-2">mdi-chevron-up</v-icon>
          </v-btn> -->
          <v-checkbox
            v-model="userAgreementCheck"
            class="mx-3 user-agreement-check"
            label=""/>
        </div>
      </div>
      <hr>
      <div class="row">
        <div class="col-xs-12 col-md-12 text-center">
          <v-btn
            :disabled="!userAgreementCheck"
            class="mx-0 my-4 p-4 h5 bg-button-color w-50"
            @click="doSubmit()">
            <v-icon class="mr-2">mdi-account-plus</v-icon>
          </v-btn>
        </div>
        <!-- <pre style="word-wrap: break-word; white-space: pre-wrap;">{{ msg }}</pre> -->
        <!-- <v-flex
          xs12
          lg2>
          <v-menu
            :v-model="false"
            :close-on-content-click="false"
            :nudge-left="40"
            lazy
            transition="scale-transition"
            max-width="290px"
            offset-y
            full-width
          >
            <v-text-field
              slot="activator"
              :class="{'danger-input' : theme==='dark' , 'general-input' : theme==='light'}"
              v-model="errorcode"
              label=""
              prepend-icon="event"
            />
            <v-date-picker
              v-model="errorcode"
              :min="new Date()"
              :max="new Date()"
              :color="theme==='dark'?'danger':'general'"
              type="month"
              no-title
              scrollable
              actions/>
          </v-menu>
        </v-flex> -->
      </div>
    </div>
    <v-snackbar
      v-model="snackbar"
      :top="true"
      :color="color">
      {{ errorMessages }}
      <v-btn
        dark
        flat
        @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
